import styled from 'styled-components';
import {Link as LinkR } from 'react-router-dom';
import {Link as LinkS} from 'react-scroll'; 

export const Nav=styled.nav`
    background:${({scrollNav})=>(scrollNav? '#000':'transparent')};
    height:80px;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:1rem;
    position:sticky;
    top:0;
    z-index:10;

    @media screen and (max-width:960){
        transition: 0.8s all ease;
    };
`

export const NavbarContainer=styled.div`
    display:flex;
    justify-content:space-between;
    height:80px;
    z-index:1;
    width:100%;
    padding 0 24px;
    max-width:1100px;
`

export const NavLogo=styled(LinkR)`
    color:#fff;
    justify-self:flee-start;
    cursor:pointer;
    font-size:1.5rem;
    display:flex;
    align-items:center;
    margin-left:24px;
    font-weight:bold;
    text-decoration:none;
`

export const MobileIcons=styled.div`
    display:None;
    @media screen and (max-width:760px){
        display:block;
        position:absolute;
        top:0;
        right:0;
        transform:translate(-100%,60%);
        font-size:1.8rem;
        cursor:pointer;
        color:white;
    }`
export const NavMenu=styled.ul`
    display:flex;
    align-item:center;
    list-style:none;
    text-align:center;
    margin-right:-22px;

    @media screen and (max-width:760px){
        display:None;
    }
`
export const NavItem=styled.li`
    height:80px;`

export const NavLinks=styled(LinkS)`
    color:white;
    display:flex;
    align-items:center;
    text-decoration:None;
    padding:0 1rem;
    height:100%;
    cursor:pointer;

    &.active{
        border-bottom: 3px solid #01bf71;
    }
`

export const NavBtn =styled.nav`
    display:flex;
    align-items:center;
    @media screen and (max-width:760px){
        display:none;
    }    
`

export const NavBtnLink =styled(LinkR)`{
    border-radius:50px;
    background:#01bf71;

    padding:10px 22px;
    color:#010606;
    font-size:16px;
    outline:none;
    border:none;
    cursor:pointer;
    transition: all 0.2 ease-in-out;
    text-decoration:none;

    &:hover{
        transiton:all 0.2 ease-in-out;
        background:#fff;
        color:#010606;
    }
}`